import { AwesomeOption } from './awesome-option';
export class AwesomeHelper {
/**
 * @param {?} options
 * @param {?} textField
 * @param {?} idField
 * @return {?}
 */
public static toAwesomeOptions(options: any[], textField: string, idField: string): AwesomeOption[] {
        if (!options) {
            return null;
        }
        const /** @type {?} */ awesomeOptions: AwesomeOption[] = [];
        options.forEach((option) => {
            awesomeOptions.push(new AwesomeOption(textField, idField, option));
        });
        return awesomeOptions;
    }
/**
 * @param {?} awesomeOptions
 * @return {?}
 */
public static fromAwesomeOptions(awesomeOptions: AwesomeOption[]): any[] {
        if (!awesomeOptions) {
            return null;
        }
        const /** @type {?} */ options: any[] = [];
        awesomeOptions.forEach((option) => {
            options.push(option.model);
        });
        return options;
    }
/**
 * @param {?} element
 * @param {?} query
 * @return {?}
 */
public static getStylesPosition(element: any, query: string) {
        const /** @type {?} */ clientRect = element
            .querySelector(query)
            .getBoundingClientRect();
        const /** @type {?} */ width = clientRect.width;
        const /** @type {?} */ height = clientRect.height;
        const /** @type {?} */ leftOffset = clientRect.left;
        const /** @type {?} */ topOffset = clientRect.top + height + window.pageYOffset;

        return `position: absolute;
                width: ${width}px;
                left: ${leftOffset}px;
                top: ${topOffset}px;
                z-index: 100000;`;
    }
/**
 * @param {?} text
 * @return {?}
 */
public static stringToLowerSafely(text: string) {
        if (!text) {
            return text;
        } else {
            return text.toLowerCase();
        }
    }
}
